@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
table {
  display: grid;
  border-collapse: collapse;
  width: 100%;
  height: 60vh;
  grid-template-columns: repeat(5, minmax(150px, 1fr));

  overflow-x: auto;
  letter-spacing: 0.075rem;
  font-size: 1.5rem;
}

thead,
tbody,
tr {
  display: contents;
}

th,
td {
  text-align: center;
  padding: 15px;
  text-overflow: ellipsis;
  /* white-space: wrap; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

th {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: var(--color-th);
  color: white;
}

th:last-child {
  border: 0;
}

td {
  padding-top: 10px;
  padding-bottom: 10px;
  color: var(--color-text);
}

tr:nth-child(even) td {
  background: var(--color-tr);
}

table::-webkit-scrollbar {
  width: 0;
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  height: 100vh;
  width: 100vw;
  padding: 5rem;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  /* 10px / 16px = 0.625 = 62.5% */
  /* For an easier way to play with the rem */
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  font-family: "Manrope", sans-serif;
  line-height: 1;
  font-weight: 400;
  color: #555;
  max-width: 100vw;
  overflow-x: hidden;
}

a {
  color: unset;
  text-decoration: none;
}

@media (max-width: 84em) {
  .app-container {
    padding: 1rem;
  }
}

.spinner-overlay {
  height: 60vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner {
  border: 2rem solid var(--loader-circle);
  border-radius: 50%;
  border-top: 2rem solid var(--color-th);
  width: 20rem;
  height: 20rem;
  animation: spin 1.2s cubic-bezier(0, 0.93, 0.77, 0.05) infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.button {
  padding: 2rem 4rem;
  background: none;
  border: 1px solid var(--color-th);
  border-radius: 1rem;
  color: var(--color-text);
  font-size: 2rem;
  outline: none;
  width: 100%;
  cursor: pointer;
}

.button:hover {
  background-color: var(--color-th);
  color: white;
  box-shadow: 3px 5px 19px -6px var(--shadow);
}

.button-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  padding-bottom: 4rem;
  grid-gap: 1rem;
  gap: 1rem;
}

@media (max-width: 84em) {
  .button-container {
    flex-direction: column;
    width: 100%;
  }

  .button {
    padding: 1rem 2rem;
    font-size: 2rem;
    width: 100%;
  }
}

.error-container {
  background: #aa7722
    url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/626404/500.gif") no-repeat;
  background-size: cover;
  background-position: center bottom;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

div {
  color: #fff;
  margin: 0 auto;
}

h1 {
  font-weight: 700;
  margin-bottom: 0;
  font-size: 20rem;
}

p {
  margin-top: 0;
  font-size: 4rem;
  padding: 5rem 0;
}

@media (max-width: 84em) {
  div {
    padding: 0;
  }

  h1 {
    font-size: 6rem;
    padding-bottom: 12rem;
  }

  p {
    font-size: 3rem;
    padding: 2rem 1rem;
  }
}

:root {
  --font-family: "Manrope", sans-serif;

  --color-th: #6671c4;
  --color-tr: rgba(227, 221, 252, 0.404);
  --color-tr-nth: #eaf7ff;

  --color-text: #808080;

  --loader-circle: #ebebeb;

  --shadow: #a3a3a375;
}

